        <el-col :span="4">
          <el-button type="primary" plain @click="send('video', {'value':3})">3</el-button>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" plain @click="send('video', {'value':4})">4</el-button>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" plain @click="send('video', {'value':5})">5</el-button>
