<script>
  import Layout from '../../components/Layout'
  import {send} from '@/api/send'
  import inImgUrl from '@/assets/in.png'
  import outImgUrl from '@/assets/out.png'

  export default {
    name: 'energy-storage-index',
    components: {
      Layout
    },
    data() {
      return {
        no: 15
      }
    },
    created() {
    },
    mounted() {
      window['moveNo'] = 1
      var no = this.no
      var leftMove = document.getElementById('leftMove'); //
      var rightMove = document.getElementById('rightMove'); //

      var ws = new WebSocket("ws://" + location.hostname + ":2346");
// ws.onopen = function () {
// 	ws.send('{"no": 8, "command": "666", "param": {}}');
// };
      var ef = []

      function init(joystick, eee) {
        var ji = new Image(); //
        var jo = new Image(); //
        var josize = joystick.height; //
        var jisize = josize * 0.3; //
        var centerX = josize / 2; //x
        var centerY = josize / 2; //y
        var touchX = joystick.getBoundingClientRect().left,
          touchY = joystick.getBoundingClientRect().top;
        // console.log(touchX)
        // console.log(joystick.getBoundingClientRect().top)
        // window.addEventListener('load', load, false);
        setTimeout(function () {
          load()
        }, 1000)
        // setTimeout('load', 2000)
        var jc = joystick.getContext('2d'); //

        //
        var jx = 0,
          jy = 0;

        //
        ji.onload = function () {
          jc.drawImage(ji, centerX - jisize / 2, centerY - jisize / 2, jisize, jisize); //
        }
        jo.onload = function () {
          jc.drawImage(jo, centerX - josize / 2, centerY - josize / 2, josize, josize);
        }

        //move
        function move() {
          // console.log(4)
          jc.clearRect(centerX - josize / 2, centerY - josize / 2, josize, josize); //
          jc.drawImage(jo, centerX - josize / 2, centerY - josize / 2, josize, josize); //
          jc.drawImage(ji, centerX - jisize / 2 + jx, centerY - jisize / 2 + jy, jisize, jisize); //
          if (window['moveNo']) {
            window.requestAnimationFrame(move); //
          }
        }

        ji.src = inImgUrl //
        jo.src = outImgUrl //

        //
        function load() {
          let t = 'touch_' + eee
          window[t] = touch
          document.addEventListener('touchstart', eval(t), false);
          document.addEventListener('touchmove', eval(t), false);
          document.addEventListener('touchend', eval(t), false);

          //
          move();
          var effectiveFinger = -1; //

          //
          function touch(event) {
            var event = event || window.event;
            // console.log(centerX)
            // console.log(centerY)
            // if (event.touches[i].clientX) {
            // 	return ;
            // }
            var oInp = document.getElementById("inp");
            switch (event.type) {
              case "touchstart":
                // console.log(event.touches)
                //
                for (var i = 0; i < event.touches.length; i = i + 1) {
                  if (Math.sqrt(Math.pow(event.touches[i].clientX - centerX - touchX, 2) +
                    Math.pow(event.touches[i].clientY - centerY - touchY, 2)) <=
                    josize / 2 - jisize / 2) {
                    effectiveFinger = i;
                    console.log("finger No." + i + " is effectiveFinger now.");
                  } else {

                  }
                }
                break;
              case "touchend": //
                for (var i = 0; i < event.touches.length; i = i + 1) {
                  if (Math.sqrt(Math.pow(event.touches[i].clientX - centerX - touchX, 2) +
                    Math.pow(event.touches[i].clientY - centerY - touchY, 2)) <=
                    josize / 2 - jisize / 2) {
                    continue;
                  } else {
                    jx = 0;
                    jy = 0;

                    let d = {
                      no: no,
                      command: eee,
                      param: {
                        x: parseFloat(jx).toFixed(6),
                        y: parseFloat(jy).toFixed(6)
                      }
                    }
                    ws.send(JSON.stringify(d));
                  }
                }
                if (event.touches[effectiveFinger] == null) {
                  if (event.touches[0] == null) {
                    jx = 0;
                    jy = 0;

                    let d = {
                      no: no,
                      command: eee,
                      param: {
                        x: parseFloat(jx).toFixed(6),
                        y: parseFloat(jy).toFixed(6)
                      }
                    }
                    ws.send(JSON.stringify(d));
                  }

                  // let d = {
                  // 	no: 99,
                  // 	command: eee,
                  // 	param: {
                  // 		x: 0,
                  // 		y: 0
                  // 	}
                  // }
                  // ws.send(JSON.stringify(d));
                  effectiveFinger -= 1;
                }
                break;
              case "touchmove": //
                //
                if (effectiveFinger != -1) {
                  if (event.touches[effectiveFinger] != undefined) {
                    if (Math.sqrt(Math.pow(event.touches[effectiveFinger].clientX - centerX - touchX, 2) +
                      Math.pow(event.touches[effectiveFinger].clientY - centerY - touchY, 2)) <=
                      josize / 2 - jisize / 2) {
                      jx = event.touches[effectiveFinger].clientX - centerX - touchX;
                      jy = event.touches[effectiveFinger].clientY - centerY - touchY;
                    } else
                    //
                    {
                      // var x = event.touches[effectiveFinger].clientX - touchX,
                      // 	y = event.touches[effectiveFinger].clientY - touchY,
                      // 	r = josize / 2 - jisize / 2;
                      // var ans = GetPoint(centerX, centerY, r, centerX, centerY, x, y);
                      // //
                      // if(Math.sqrt((ans[0] - x) * (ans[0] - x) + (ans[1] - y) * (ans[1] - y)) < Math.sqrt((ans[2] - x) * (ans[2] - x) + (ans[3] - y) * (ans[3] - y))) {
                      // 	jx = ans[0] - centerX;
                      // 	jy = ans[1] - centerY;
                      // } else {
                      // 	jx = ans[2] - centerX;
                      // 	jy = ans[3] - centerY;
                      // }
                    }
                    //move();
                    let d = {
                      no: no,
                      command: eee,
                      param: {
                        x: Number.parseFloat(jx / (centerY - jisize / 2)).toFixed(6),
                        y: Number.parseFloat(jy / (centerY - jisize / 2)).toFixed(6)
                      }
                    }
                    // console.log(d)
                    ws.send(JSON.stringify(d));
                    // console.log(jx / (centerY - jisize / 2), jy / (centerY - jisize / 2))
                    // event.preventDefault(); //
                  }
                }
                break;
            }
          }

          window.requestAnimationFrame(move); //
        }
      }

      init(leftMove, "leftMove")
      init(rightMove, 'rightMove')


//
//cxcy r
//(stx,sty)(edx,edy)
      function GetPoint(cx, cy, r, stx, sty, edx, edy) {
        //(x-cx)^2+(y-cy)^2=r^2
        //y=k*x+b
        var k = (edy - sty) / (edx - stx);
        var b = edy - k * edx;
        //(1 + k^2)*x^2 - x*(2*cx -2*k*(b -cy) ) + cx*cx + ( b - cy)*(b - cy) - r*r = 0
        var x1, y1, x2, y2;
        var c = cx * cx + (b - cy) * (b - cy) - r * r;
        var a = (1 + k * k);
        var b1 = (2 * cx - 2 * k * (b - cy));

        var tmp = Math.sqrt(b1 * b1 - 4 * a * c);

        x1 = (b1 + tmp) / (2 * a);
        y1 = k * x1 + b;

        x2 = (b1 - tmp) / (2 * a);
        y2 = k * x2 + b;
        return [x1, y1, x2, y2];
      }
    },
    destroyed() {
      window['moveNo'] = 0
      document.removeEventListener('touchmove', touch_leftMove, false)
      document.removeEventListener('touchmove', touch_rightMove, false)
      document.removeEventListener('touchstart', touch_leftMove, false)
      document.removeEventListener('touchstart', touch_rightMove, false)
      document.removeEventListener('touchend', touch_leftMove, false)
      document.removeEventListener('touchend', touch_rightMove, false)
    },
    methods: {
      send(command, param) {
        let sendData = {}
        sendData.no = this.no
        sendData.command = command
        sendData.param = param
        send(sendData).then(response => {

        })
      }
    }
  }
</script>
